.search-container {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: #00000080;
  .close {
    opacity: 0.9;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #ecc02a;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .search {
    width: 50%;
    height: 50px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      padding: 0 20px;
      box-shadow: 0px 0px 10px 0px #ecc02a80;
      &::placeholder {
        font-family: ar-reg;
      }
    }
    &::after {
      content: "";
      font-family: "Linearicons-Free";
      font-size: 18px;
      color: rgb(15, 15, 15);
      position: absolute;
      top: 50%;
      left: 20px;
      transition: all .5s ease-in-out;
      transform: translateY(-50%);
    }
  }
}




/*------ navbar start ------*/
.nav-bar {
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 13px 0;
  }
  .container {
    position: relative;
  }
  .logo-container {
    width: 260px;
    .logo {
      width: 165px;
    }
  }
  .icons {
    width: 260px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    button {
      font-size: 18px;
      color: rgb(0, 0, 0);
      position: relative;
      padding: 0;
      margin: 0 0 0 20px;
      transition: all .3s ease-in-out;
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        font-family: ar-reg;
        font-size: 14px;
        color: #000;
        padding: 0 5px;
        transition: all .3s ease-in-out;
      }
      &:last-child {
        margin: 0;
      }
      &:hover {
        color: #ecc02a;
        span {
          color: #ecc02a;
        }
      }
    }
    .menu-icon {
      display: none;
    }
    .cart-icon {
      .counter {
        position: absolute;
        top: -9px;
        right: -7px;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background-color: #5367f2;
        color: rgb(255, 255, 255);
        font-family: ar-reg;
        font-size: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .5s ease-in-out;
      }
      &:hover {
        .counter {
          transform: rotateY(360deg);
        }
      }
    }
  }
  .lang-switcher {
    position: absolute;
    top: 15px;
    left: 0px;
    font-family: ar-reg;
    font-size: 12px;
    color: #939393;
    margin: 0 30px 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      margin: 0 5px;
    }
    > span {
      cursor: pointer;
      transition: all .3s ease-in-out;
      &::after {
        content: ""; 
        font-family: "Linearicons-Free";
        font-weight: 600;
        font-size: 8px;
        color: #939393;
        padding: 0 5px;
        transition: all .3s ease-in-out;
      }
    }
    .switch {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 40px;
      left: 50%;
      z-index: 9;
      transform: translateX(-50%);
      width: 90px;
      background-color: #fff;
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.09);
      color: #000;
      border-radius: 5px;
      padding: 5px 0;
      transition: all .5s ease-in-out;
      a {
        display: block;
        text-align: center;
        margin: 5px 0;
        transition: all .3s ease-in-out;
        &:hover {
          color: #ecc02a;
        }
      }
    }
    &:hover {
      > span {
        color: #ecc02a;
        &::after {
          color: #ecc02a;
        }
      }
      .switch {
        top: 20px;
        opacity: 1;
        visibility: visible;
      }
    }
  }
}


/*------ navbar end ------*/



