/*------ header start ------*/
.header {
    .owl-carousel {
      position: relative;
      .owl-nav {
        .owl-prev, .owl-next {
          outline: none;
          // width: 60px;
          // height: 60px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          font-size: 41px;
          background-color: rgba(0, 0, 0, 0.0);
