          right: 25px;
        }
        .owl-next {
          left: 25px;
        }
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: 30px;
        transform: translateX(-50%);
        .owl-dot {
          width: 9px;
          height: 9px;
          background-color: #00000080;
          border-radius: 50%;
          outline: none;
          margin: 0 2.5px;
