        &::after {
          color: #ecc02a;
        }
      }
      .switch {
        top: 20px;
        opacity: 1;
        visibility: visible;
      }
    }
  }

  .fixed-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 999;
    .menu {
      width: 260px;
      height: 100%;
      position: absolute;
      top: 0;
      right: -280px;
      background-color: #fff;
      padding: 20px 20px;
      overflow-y: auto;
      transition: all .5s ease-in-out;
      &.show {
        right: 0;
      }
    }
    .menu-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .close {
        opacity: 1;
        color: #444;
        font-size: 17px;
        position: absolute;
        top: 20px;
        left : 15px;
      }
    }
    .main-list {
      margin: 35px 0px;
      transform: translateX(-50%);
      transition: all .5s ease-in-out;
      .slide {
        cursor: pointer;
        position: relative;
        &::after {
          content: "";
          font-family: "Linearicons-Free";
          font-size: 8px;
          color: #0f0f0f;
          position: absolute;
          top: 8px;
          left: 10px;
          transition: all .5s ease-in-out;
        }
        &.active {
          color: #ecc02a;
          &::after {
            content: "";
          }
        }
      }
      > li {
        font-family: ar-md;
        font-size: 16px;
        color: #444;
        margin: 10px 0;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
      &.show {
        transform: translateX(0%);
      }
    }
    .sub-list {
      padding: 5px 10px;
      display: none;
      .slide-two {
        cursor: pointer;
        position: relative;
        
        &::after {
          content: "";
          font-family: "Linearicons-Free";
          font-size: 8px;
          color: rgb(15, 15, 15);
          position: absolute;
          top: 9px;
          right: 15px;
          transition: all .5s ease-in-out;
        }
        &.active {
          color: #000;
          &::after {
            content: "";
          }
        }
      }
      li {
        font-family: ar-reg;
        font-size: 16px;
        color: #444;
        margin: 5px 0;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
    }
    .sub-list-copy {
      padding: 5px 15px;
      display: none;
      li {
        font-family: ar-reg;
        font-size: 15px;
        color: #444;
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
    }
  }



