/*------ direction ------*/
html[dir="ltr"] {
  .nav-bar .lang-switcher {
    left: unset;
    right: 0;
    margin: 0 0 0 30px;
  }
  .nav-bar .icons button {
    margin: 0 20px 0 0;
    &:last-child {
      margin: 0;
    }
  }
  .nav-bar .icons .cart-icon .counter {
    right: unset;
    left: -7px;
  }
  .search-container .search::after {
    left: unset;
    right: 20px;
  }
  .news .subscribe button {
    left: unset;
    right: 5px;
  }
  .footer ul i {
    margin: 0 15px 0 0;
  }
  @media (max-width: 992px) {
    .fixed-menu .menu {
      right: unset;
      left: -280px;
      &.show {
        left: 0;
      }
    }
    .fixed-menu .menu-header .close {
      left: unset;
      right: 15px;
    }
    .fixed-menu .main-list .slide::after {
      left: unset;
      right: 10px;
    }
    .fixed-menu .main-list .slide.active {
      &::after {
        content: "";
      }
    }
    .footer .list-header::after {
      left: unset;
      right: 0;
    }
    .footer .list-header.active {
      &::after {
        content: "";
      }
    }
  }
}